<template>
  <v-card elevation="0" class="mx-auto" color="#F2F4F4" height="100vh">
    <div v-if="items.length == 0">
      <p class="text-center">..Sin Contactos...</p>
    </div>

    <div v-else>
      <v-virtual-scroll :items="items" :item-height="80" style="height: 91vh">
        <template v-slot:default="{ item }">
          <v-card elevation="0" outlined>
            <div>
              <v-list-item @click="open(item)">
                <v-list-item-action>
                  <v-btn
                    fab
                    small
                    depressed
                    color="#273746"
                    class="white--text"
                  >
                    {{ item.reference }}
                  </v-btn>
                </v-list-item-action>

                 <v-list-item-content>
                  <v-list-item-title>
                    <strong> {{ item.location }}</strong>
                  </v-list-item-title>
               <!--    <v-list-item-subtitle>{{
                    item.location
                  }}</v-list-item-subtitle> -->
                </v-list-item-content>

                <v-list-item-action>
                  <v-icon> toc </v-icon>
                </v-list-item-action>
              </v-list-item>
            </div>
          </v-card>
        </template>
      </v-virtual-scroll>
    </div>

    <board-new ref="form"/>
  </v-card>
</template>
<script>
import { mapState } from "vuex";
import BoardNew from "./ContactNew.vue";
export default {
  components: { BoardNew },
  name: "ContactList",
  data(){
    return {
      /* items:[], */
    }
  },
   /*  created(){
    console.log(this.items)
  }, */
  computed: {
    ...mapState({
      data: (state) => state.customers.findCustomer,
    }),
    items() {
      
      return this.data.contactList.data;
    },
    config() {
      return this.data.contactList.config;
    },
  },
  methods: {
      open(item) {
      this.$refs.form.openForm(item);
     /*  item.result.forEach((element) => {
        console.log(element.value);
      }); */
    },
  },
};
</script>